.Block{
    position: relative;
    padding: 10px 0;
    border-bottom: 2px solid rgb(236, 236, 236);
    width: 240px;
}

.HeaderDiv:hover{
    cursor: pointer;
}

.Header{
    background: none;
	color: inherit;
	border: none;
	font: inherit;
	cursor: pointer;
    outline: inherit;
    min-width: 210px;
    text-align: left;
    padding: 5px 10px;
    line-height: 25px;
    font-size: 14px;
    font-weight: bold;
    color: rgb(89, 89, 89);
}

.subTextA{
    text-decoration: none;
}

.Subtext{
    margin: 0;
    font-size: 15px;
    padding: 8px 10px 8px 30px;
    vertical-align: middle;
    margin: 6px 0;
    border-top-right-radius: 40px;
    border-bottom-right-radius: 40px;
    color: rgb(56, 56, 56);
}

.Subtext:hover{
    background-color: rgb(223, 223, 223);
    cursor: pointer;
}

.Activate{
    background-color: rgb(233, 240, 253);
    color: rgb(41, 90, 185);
    font-weight: 600;
}

.hidden {
    display: none;
}