.WithSidebar{
    position: absolute;
    background-color: rgb(236, 236, 236);
    margin: 0 auto;
    width: 100%;
    overflow: hidden;
}

.MainContent{
    box-sizing: border-box;
    position: relative;
    background-color: rgb(236, 236, 236);
    padding: 26px 30px 0 250px;
    width: 100%;
    min-height: 3800px;
}

.Contents{ 
    background-color: white;
    box-sizing: border-box;
    width: 1175px;
    padding: 60px 32px 50px 32px;
    margin: -50px auto 0 auto;
    align-items: center;
    min-height: 3800px;
}
/* =============================================== */
@media screen and (max-width: 1410px) {
    .MainContent{
        padding-right: 0;
    }
    .Contents{
        width: 100%;
    }
}

@media screen and (max-width: 680px) {
    .MainContent{
        padding: 5px 0 50px 0;
    }
}
