@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;1,200;1,300;1,400&display=swap');

.Navbar {
    position: fixed;
    top: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: white;
    align-items: center;
    height: inherit;
    height: 60px;
    z-index: 999;
    border-bottom: 4px solid rgb(236, 236, 236);
    
}

.Logo{
    display: inline;
    vertical-align: middle;
    padding: 0 32px 0 12px;
}

.Logo p{
    display: inline;
    margin: 0;
}

.Navitems{
    display: inline;
    vertical-align: middle;
}

.Navitems ul {
    list-style-type: none;
    display: inline;
    margin: 0;
    padding: 0;
}

.Navitems li {
    display: inline;
}

.Navitems button{
    font-family: 'Montserrat';
    text-decoration: none;
    border: none;
    font-size: 16px;
    background-color: inherit;
    padding: 0 12px;
    height: 60px;
    color: rgb(95, 95, 95)
}

.Navitems button:focus{
    outline: none;
}

.mainButton:hover{
    color: #c00000;
    cursor: pointer;
}

.rightFlex{
    padding-right: 12px;
}

.rightFlex ul {
    list-style-type: none;
    display: inline;
    margin: 0;
    padding: 0;
}

.rightFlex li {
    display: inline;
    margin: 0 4px;
}

.rightFlex img{
    width: 24px;
}

.rightFlex img{
    cursor: pointer;
}

.dropdown{
    position: absolute;
    margin-top: 6px;
    min-height: 200px;
    width: 200px;
    text-align: left;
    background-color: rgb(255, 255, 255);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    padding: 18px;
}



.item{
    font-size: 16px;
    color: rgb(95, 95, 95);
}


.dropdown a{
    text-decoration: none;
}

.dropdown p{
    margin: 0;
}

.SubHeader{
    font-size: 16px;
    font-weight: bold;
}

.dropdown li{
    display: block;
    padding: 4px;
}


@media screen and (max-width: 832px) {
    .rightFlex{
        display: none;
    }
}

@media screen and (max-width: 680px) {
    .Navitems{
        display: none;
    }

    .Navbar{
        justify-content: center;
    }
}

@media screen and (max-width: 370px) {
    .Navbar{
        justify-content: flex-end;
    }

}