.Boxdiv{
    box-sizing: content-box;
    margin: 10px 16px;
    border-radius: 4px;
    width: 30.8%;
    height: 480px;
    background-color:  rgb(236, 236, 236);
    text-align: center;
    justify-content: center;
    overflow: hidden;
    position: relative;
}

.Boxdiv h2{
    margin: 20px 0 8px 0;
    font-weight: normal;
    font-size: 18px;
}

.Boxdiv h3{
    margin: 0 56px;
    font-weight: 500;
}

.Boxdiv p{
    margin: 8px 20px 36px 20px;
    font-size: 14px;
}

.Boxdiv img{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    margin-top: 60px;
}

@media screen and (max-width: 1264px) {
    .Boxdiv{
        width: 30%;
        height: 490px;
    }
}

@media screen and (max-width: 948px) {
    .Boxdiv{
        width: 46%;
        height: 490px;
    }
}

@media screen and (max-width: 796px) {
    .Boxdiv{
        width: 45%;
        height: 490px;
    }
}

@media screen and (max-width: 632px) {
    .Boxdiv{
        width: 100%;
        height: 490px;
    }

    .Boxdiv img{
        bottom: -50px;
    }
}

@media screen and (max-width: 530px) {
    .Boxdiv{
        width: 100%;
        height: 430px;
    }
}

@media screen and (max-width: 430px) {
    .Boxdiv{
        height: 420px;
    }
}

@media screen and (max-width: 375px) {
    .Boxdiv h3{
        font-size: 16px;
    }

    .Boxdiv img{
        bottom: 0;
    }
}