.LeftSideBar{
    position: fixed;
    overflow: scroll;
    height: 100%;
    width: 250px;
    border-right: 2px solid rgb(236, 236, 236);
    background-color: white;
    z-index: 997;
}

.Main{
    margin: 0;
    padding: 12px 10px;
    border-bottom: solid rgb(236, 236, 236) 4px;
    font-size: 16px;
    font-weight: bold;
}

.LeftSideBar a{
    text-decoration: none;
    color: rgb(44, 44, 44);
}


@media screen and (max-width: 680px) {
    .LeftSideBar{
        left: -250px;
    }

    .notHidden{
        left: 0;
    }
}
