.Img{
    box-sizing: border-box;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 4px;
    border: rgb(202, 202, 202);
    border-style: solid;
    border-width: 1px;

}
.W200{
    width: 200px;
}

.W400{
    width: 400px;
}

.W600{
    width: 700px;
}

@media screen and (max-width: 500px) {
    .W400{
        width: 100%;
    }
}


@media screen and (max-width: 1000px) {
    .W600{
        width: 100%;
    }
}