@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,500;1,600;1,700&display=swap');

.darkpurple{
    color: rgb(162, 78, 159);
    font-weight: bold;
}

.red{
    color: rgb(193, 62, 64);
}

.blue{
    color: rgb(52, 123, 171);
}

.darkblue{
    color: blue;
}

.grey{
    color: rgb(155, 127, 139)
}


/* Div CSS */
.topSwiftDiv{
    box-sizing: border-box;
    background-color: rgb(246, 246, 246);
    border-top: rgb(181, 181, 181) 1px solid;
    border-left: rgb(181, 181, 181)1px solid;
    border-right:rgb(181, 181, 181) 1px solid;
    border-bottom:rgb(181, 181, 181) 1px solid;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}

.topSwiftDiv p{
    font-size: 12px;
    margin: 0;
    padding: 10px 8px 10px 16px;
    font-weight: bold;
}

.btmSwiftDiv{
    margin: 0;
    padding: 12px 16px;
    border-bottom: rgb(181, 181, 181) 1px solid;
    border-left: rgb(181, 181, 181)1px solid;
    border-right:rgb(181, 181, 181) 1px solid;
}

.btmRadius{
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}

.btmSwiftDiv code{
    margin: 0;
    font-size: 14px;
    font-family: 'Roboto Mono', monospace;
}

.resultTopSwiftDiv{
    box-sizing: border-box;
    background-color: rgb(246, 246, 246);
    border-left: rgb(181, 181, 181)1px solid;
    border-right:rgb(181, 181, 181) 1px solid;
    border-bottom:rgb(181, 181, 181) 1px solid;
}
.resultTopSwiftDiv p {
    font-size: 12px;
    margin: 0;
    padding: 6px 8px 6px 16px;
    font-weight: bold;
}

.resultSwiftDiv{
    margin: 0;
    padding: 12px 16px;
    border-bottom: rgb(181, 181, 181) 1px solid;
    border-left: rgb(181, 181, 181)1px solid;
    border-right:rgb(181, 181, 181) 1px solid;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}


.result{
    margin: 0;
    font-size: 14px;
}