.Home{
    max-width: 1380px;
    margin: 0 auto;
    padding-top: 1px;
}

.banner{
    width: 97%;
    height: 300px;
    margin: 18px auto 14px auto;
    background-color: rgb(236, 236, 236);
    border-radius: 4px;
    overflow: hidden;
    text-align: center;
    justify-content: center;
}
.BannerImg{
    margin-top: 30px;
    margin-left: -100%;
    margin-right: -100%;
}

.BoxItems{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.banner h1{
    margin: 0;
    text-align: center;
}

.small{
    padding-top: 50px;
    font-size: 20px;
}

.large {
    font-size: 32px;
}

.banner p{
    margin: 0;
    padding: 0 20px;
    text-align: center;
}

.smallp{
    font-size: 12px;
}

@media screen and (max-width: 832px) {
    .banner{
        height: 320px;
    }
}

@media screen and (max-width: 460px) {
    .banner{
        height: 350px;
    }
}


@media screen and (max-width: 375px) {
    .BannerImg{
        margin-left: -105%;
    }
}

@media screen and (max-width: 320px) {
    .BannerImg{
        margin-left: -135%;
    }
}

@media screen and (max-width: 294px) {
    .banner{
        height: 380px;
    }
}

@media screen and (max-width: 222px) {
    .BannerImg{
        margin-left: -160%;
    }
}