.FilledButton{
    position: relative;
    margin: 2px;
    padding: 10px 20px;
    display: inline-block;
    color: rgb(242, 242, 242);
    border-radius: 25px;
    vertical-align: middle;
}

.FilledButton img{
    vertical-align: middle;
    margin-right: 10px;
    width: 20px;
}

.blue{
    background-color: rgb(44, 113, 219);
}

.red{
    background-color: rgb(188, 39, 26);
}

.blue:hover{
    background-color: rgb(93, 146, 226);
    cursor: pointer;
}

.red:hover{
    background-color: rgb(223, 114, 104);
    cursor: pointer;
}